.btn {
  @apply font-medium py-2 px-4 rounded text-base leading-none whitespace-no-wrap overflow-hidden;
}

.btn-primary {
  @apply bg-indigo-700 text-white;

  &:hover,
  &:focus {
    @apply bg-indigo-800;
  }
}

.btn-icon {
  @apply pl-7 relative;

  .btn-icon-wrapper {
    @apply absolute top-0 bottom-0 my-auto left-0 w-7 h-7 flex items-center justify-center;

    .icon {
      @apply w-4 h-4;
    }
  }
}

.btn-only-icon {
  @apply relative w-7 h-7 p-0;

  .icon {
    @apply absolute inset-0 m-auto w-4 h-4;
  }
}

.btn-link-primary {
  @apply text-indigo-700;

  &:hover,
  &:focus {
    @apply text-indigo-800;
  }
}

.btn-link-danger {
  @apply text-red-500;

  &:hover,
  &:focus {
    @apply text-red-500;
  }
}
